import { defineStore } from 'pinia'

// store
export const useTraceStore = defineStore('trace', {
  state: () => ({
    results: [],         // 
    currentPage: 1,      // 
    itemsPerPage: 10,    // 
    selectedPlatform: '', // 
    loading: false,      // 
    errorMessage: ''     // 
  }),

  getters: {
    // 
    filteredResults(state) {
      if (!state.selectedPlatform) {
        return state.results
      }
      return state.results.filter(item => item.datasource === state.selectedPlatform)
    },

    // 
    totalPages(state) {
      return Math.ceil(this.filteredResults.length / state.itemsPerPage)
    },

    // 
    paginatedResults(state) {
      const startIndex = (state.currentPage - 1) * state.itemsPerPage
      return this.filteredResults.slice(startIndex, startIndex + state.itemsPerPage)
    }
  },

  actions: {
    // 
    setResults(data) {
      this.results = data
      this.currentPage = 1  // 
    },

    // 
    setCurrentPage(page) {
      this.currentPage = page
    },

    // 
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--
      }
    },

    // 
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++
      }
    },

    // 
    resetPagination() {
      this.currentPage = 1
    },

    // 
    setSelectedPlatform(platform) {
      this.selectedPlatform = platform
      this.currentPage = 1  // 
    },

    // 
    setLoading(loading) {
      this.loading = loading
    },

    // 
    setErrorMessage(message) {
      this.errorMessage = message
    }
  }
})