<template>
  <!--  -->
  <div class="fixed inset-0 bg-black bg-opacity-70 flex items-center justify-center z-50" @click="$emit('close')">
    <!--  -->
    <div class="relative max-w-full p-4" @click.stop>
      <h1 class="text-xl font-bold mb-6 text-white"></h1>
      <!--  -->
      <div v-if="videoData">
        <video controls class="max-w-full h-auto rounded-lg shadow-md">
          <source :src="videoData" type="video/mp4">
          
        </video>
      </div>
      <div v-else>
        <p class="text-gray-300"></p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // URL
  props: {
    videoUrl: String,
    showClose: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      videoData: null // 
    };
  },
  created() {
    // propsURLlocalStorage
    this.videoData = this.videoUrl;
    // localStorage
    if (!this.videoUrl) {
      localStorage.removeItem('tempVideo');
    }
  }
};
</script>