    <!--  -->
    <div class="bg-white rounded-xl shadow-lg p-6 mb-6">
      <h3 class="text-xl font-semibold text-gray-800 mb-4"> </h3>
      
      <div class="space-y-6">
        <!--  -->
        <div class="w-full">
          <label for="queryContent" class="block text-base font-medium text-gray-700 mb-1"></label>
